/*шрифты*/
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
}
.page {
  height: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
}
.page__body {
  box-sizing: border-box;
  min-height: 100%;
  margin: 0 auto;
  background-color: #ffffff;
  color: #000000;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-header__wrapper {
  padding: 10px 20px 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-header__nav {
  display: none;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    padding: 24px 30px 0 30px;
  }
  .main-header__logo-cat-energy {
    display: none;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-header__nav {
    display: block;
  }
}
@media (min-width: 1440px) {
  .main-header__wrapper {
    padding: 53px 110px 0 110px;
  }
}
.main-nav {
  width: 100%;
}
.main-nav__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.main-nav__item {
  margin: 0;
  width: 100%;
}
.main-nav__item a {
  display: block;
  cursor: pointer;
  font-family: inherit;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  min-height: 66px;
  text-align: center;
  padding: 22px 0 24px 0;
}
@media (min-width: 768px) {
  .main-nav {
    min-width: 440px;
    width: auto;
  }
  .main-nav__list {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
    justify-content: space-between;
  }
  .main-nav__item {
    width: auto;
  }
  .main-nav__item a {
    font-size: 18px;
    line-height: 24px;
    min-height: auto;
    text-align: center;
    padding: 0;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    min-width: 480px;
  }
  .main-nav__item a {
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
  }
}
.promo__wrapper {
  padding: 27px 20px 0 20px;
}
.promo__wrapper-background {
  background: linear-gradient(to right, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-background-desktop-mobile.jpg");
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  padding: 27px 35px 164px 35px;
  margin: -27px -20px 0 -20px;
  color: #ffffff;
}
