}
.promo__subtitle {
  margin: 0;
  font-family: inherit;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
}
.promo__button-choose-program {
  box-sizing: border-box;
  min-width: 100%;
  min-height: 40px;
  border: 0;
