    background: #ffffff;
    color: #000000;
    margin: 0;
    padding: 0;
  }
  .promo__title {
    text-align: left;
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 40px;
  }
  .promo__subtitle {
    font-size: 20px;
    line-height: 20px;
    text-align: left;
  }
  .promo__image {
    display: none;
  }
